import React from 'react';

export default function MultiColor({ handleonClick, handleonChange }) {
  return (
    <ul className="list">
      <label>Multi Color</label>
      <li className="list__item">
        <input
          type="radio"
          className="radio-btn"
          name="choice"
          id="a-opt"
          onClick={handleonClick}
          onChange={e => handleonChange(e, 'multi')}
          value={true}
        />
        <label htmlFor="a-opt" className="label">
          Si
        </label>
      </li>

      <li className="list__item">
        <input
          type="radio"
          className="radio-btn"
          name="choice"
          id="b-opt"
          onClick={handleonClick}
          onChange={e => handleonChange(e, 'multi')}
          value={false}
        />
        <label htmlFor="b-opt" className="label">
          No
        </label>
      </li>
    </ul>
  );
}
