import React from 'react';

export default function Ready({ orden }) {
  const {
    ordenID,
    descrip,
    money,
    acabado,
    multi,
    apcombo,
    servicio,
    cash,
    url,
  } = orden;

  function total() {
    let resultado = parseInt(cash);

    if (apcombo) {
      let descuento = parseInt(cash) * 0.1;
      resultado = parseInt(cash) - descuento;
    }
    return resultado;
  }
  return (
    <div className="container ready-grid centerX ">
      <div className="card">
        <h2>{descrip}</h2>
        <hr />
        <img src={url} alt="" />
      </div>
      <div className="card">
        <h2>Compra</h2>
        <hr />
        <p>
          <strong>Id Orden:</strong> {ordenID}
        </p>
        <p>Esmalte: {money}</p>
        <p>
          <strong>Multi Color:</strong> {multi ? 'c1350' : 'no'}
        </p>
        <p>
          <strong>Aplica Combo:</strong> {apcombo ? 'si' : 'no'}
        </p>
        {apcombo ? (
          <div>
            <p>Servicio: c{servicio} </p>
            <p>Acabado: c{acabado}</p>
            <p>Aplica descuento del 10% : c{parseInt(cash) * 0.1}</p>
          </div>
        ) : (
          <p></p>
        )}
        <button>Total: c{total()}</button>
      </div>
    </div>
  );
}

