import React from 'react';

export default function Product({ item, setIsOrder, setOrden }) {
  const { id, url, descrip, apcombo, money, multi } = item;

  const handleonClick = e => {
    setOrden({
      ordenID: id,
      descrip: descrip,
      money: money,
      acabado: '0',
      multi: multi,
      apcombo: apcombo,
      servicio: '0',
      cash: '0',
      url: url,
    });
    setIsOrder(true);
  };

  return (
    <div className="product-item">
      <img src={url} alt={url} />
      <h1>{descrip.substr(0, 10)}</h1>
      <p className="product-cost">
        Tan solo a: c{money}{' '}
        <span className="oferta">{apcombo ? ' y 10% descuento' : ''}</span>{' '}
      </p>
      <button className="button-primary product-btn" onClick={handleonClick}>
        Comprar
      </button>
      <p className="multi-color">{multi ? 'Esta en Multi color' : null}</p>
    </div>
  );
}
