import React from 'react';

export default function AppCompo({ handleonClick, handleonChange }) {
  return (
    <div>
      <div>
        <label>Acabado</label>
        <div className="select">
          <select
            name="acabado"
            id="slct"
            onClick={handleonClick}
            onChange={e => handleonChange(e, 'acabado')}
          >
            <option defaultChecked value="0">
              Ninguno
            </option>
            <option value="4750">Resina c4750</option>
            <option value="3500">Plástico c3500</option>
            <option value="1500">Natural c1500</option>
          </select>
        </div>
      </div>

      <div>
        <label>Servicios</label>
        <div className="select">
          <select
            name="servicio"
            id="slct"
            onClick={handleonClick}
            onChange={e => handleonChange(e, 'servicio')}
          >
            <option defaultValue value="0">
              Ninguno
            </option>
            <option value="4500">Manicure c4500</option>
            <option value="2000">Encapsulado c2000</option>
          </select>
        </div>
      </div>
    </div>
  );
}
