import React from "react";
import logo from "../img/logo_U_bellas.jpg";

function TitleBar() {
  return (
    <div className="container-logo container center">
      <h1 className="logo-title">Uñas Bellas</h1>
      <img className="logo" src={logo} alt="Uñas Bellas" />
    </div>
  );
}

export default TitleBar;
