import React, { useState, Fragment } from 'react';
import MultiColor from './MultiColor';
import AppCompo from './AppCompo';
import Ready from './Ready';

export default function MakeOrder({ orden, setOrden }) {
  const { url, descrip, apcombo, money, acabado, servicio } = orden;

  const [cashTotal, setcashTotal] = useState([parseInt(money), 0, 0]);
  const [finalOrder, setfinalOrder] = useState(false);
  const [error, setError] = useState(false);

  const handleonChange = (e, type) => {
    if (type === 'acabado') {
      setcashTotal([
        parseInt(money),
        parseInt(e.target.value.slice(-4)),
        cashTotal[2],
      ]);
    } else if (type === 'servicio') {
      setcashTotal([
        parseInt(money),
        cashTotal[1],
        parseInt(e.target.value.slice(-4)),
      ]);
    }
    if (type === 'multi') {
      if (e.target.value === 'true') {
        setcashTotal([cashTotal[0] + 1350, cashTotal[1], cashTotal[2]]);
      } else {
        setcashTotal([parseInt(money), cashTotal[1], cashTotal[2]]);
      }
    }

    setOrden({
      ...orden,
      [type]: e.target.value,
    });
  };

  const handleSubmit = e => {
    e.preventDefault();

    //  validar la información
    if (apcombo) {
      if (acabado.trim() === '' || acabado === '0') {
        setError(true);
        return;
      }
      if (servicio.trim() === '' || servicio === '0') {
        setError(true);
        return;
      }
    }
    setOrden({ ...orden, ['cash']: suma() });
    setfinalOrder(true);
  };
  const handleonClick = e => {
    setError(false);
  };
  function suma() {
    return cashTotal[0] + cashTotal[1] + cashTotal[2];
  }

  return (
    <Fragment>
      <div className="container">
        {finalOrder ? (
          <Ready orden={orden} />
        ) : (
          <div className="show-Info">
            <img src={url} alt="#" />
            <div>
              <h1>{descrip}</h1>
              <p>
                Valor de compra: {suma()}{' '}
                <span className="oferta">
                  {apcombo ? ' y 10% descuento' : ''}
                </span>
              </p>

              <form onSubmit={handleSubmit}>
                {apcombo ? (
                  <AppCompo
                    handleonClick={handleonClick}
                    handleonChange={handleonChange}
                  />
                ) : (
                  <MultiColor
                    handleonClick={handleonClick}
                    handleonChange={handleonChange}
                  />
                )}

                {error ? (
                  <label className="error-msm">
                    Te hace falta seleccionar un item
                  </label>
                ) : (
                  <button type="submit" className="button-primary">
                    Aceptar
                  </button>
                )}
              </form>
            </div>
          </div>
        )}
      </div>
    </Fragment>
  );
}
